
body {
    margin: 0;
    padding: 0;
    background-image: url(/static/media/noisy-background.9a6cf417.png);
    font-family: 'Poiret One', cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  h1 {
    font-size: 3rem;
    font-weight: 900;
    margin: 2vw;
  }
  
  p {
    font-size: 1.5rem;
    font-weight: 600;
  }
  
  .react-svg {
    -webkit-animation: react-svg-spin infinite 20s linear;
            animation: react-svg-spin infinite 20s linear;
    height: 12vh;
  }
  
  @-webkit-keyframes react-svg-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes react-svg-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  footer {
    background-image: url(/static/media/noisy-footer.086dbdcb.png);
  }
  
