body {
    margin: 0;
    padding: 0;
    background-image: url('../img/backgrounds/noisy-background.png');
    font-family: 'Poiret One', cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  h1 {
    font-size: 3rem;
    font-weight: 900;
    margin: 2vw;
  }
  
  p {
    font-size: 1.5rem;
    font-weight: 600;
  }
  
  .react-svg {
    animation: react-svg-spin infinite 20s linear;
    height: 12vh;
  }
  
  @keyframes react-svg-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  footer {
    background-image: url('../img/backgrounds/noisy-footer.png');
  }
  